/* Mobile */
@media screen and (max-width: 768px) {
    .icon-Tasks {
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        width: 26vw;
        margin-top: 18vh;
    }

    .Tasks-title {
        font-family: 'Karantina', cursive;
        font-size: 16vw;
        margin-top: 34vh;
        width: 100%;
        text-align: center;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        color: rgb(86, 195, 232);
    }

    .subTitle1-tasks {
        font-family: 'Heebo', sans-serif;
        font-size: 5vw;
        color: grey;
        text-align: center;
        margin-top: 49vh;
        width: 82vw;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        direction: rtl;
    }

    .array-tasks {
        font-family: 'Heebo', sans-serif;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        text-align: center;
        color: #595959;
        margin-top: 60vh;
    }

    .tasksDiv {
        height: 6vh;
        width: 85vw;
        background-color: #d9d9d9;
        margin-top: 2vh;
        padding-top: 1.5vh;
        font-size: 5vmin;
        cursor: pointer;
        margin-bottom: 3vh;
    }

    .tasksDiv:hover {
        background-color: #56c3e8;
        color: white;
    }

    .part2-tasks {
        max-width: 800px;
        position: absolute;
        right: 50%;
        transform: translateX(50%);
        direction: rtl;
        top: 37vh;
    }

    .btn-to-tasksPart1 {
        margin-top: 10vh;
        margin-bottom: 10vh;
        cursor: pointer;
        color: #56c3e8;
        text-decoration: underline;
        text-align: center;
        font-family: 'Heebo', sans-serif;
        font-size: 1.5rem;
    }

    .selected-task-title {
        font-size: 1.5rem;
        margin-top: 8vh;
        margin-bottom: 20px;
        text-align: center;
        font-weight: bold;
        font-family: 'Heebo', sans-serif;
        color: #595959;
    }

    .selected-task-prefix {
        font-weight: bold;
        color: #555;
        margin-left: 5px;
        font-size: 4vmin;

    }

    .selected-task-id {
        font-weight: bold;
        color: rgb(35 88 132);
        font-size: 1.5rem;

    }

    .sub-title-only {
        font-weight: bold;
        color: rgb(35 88 132);
        font-size: 1rem;
        text-decoration: underline;
        margin-bottom: 2vh;
    }


    .task-section {
        margin-bottom: 40px;
        width: 85vw;
    }

    .section-title {
        font-size: 1.4rem;
        margin-bottom: 10px;
        color: rgb(35 88 132);
        text-align: right;
        border-bottom: 2px solid rgb(35 88 132);
        padding-bottom: 5px;
        font-family: 'Heebo', sans-serif;
    }

    .task-list {
        padding-right: 20px;
        list-style: none;
        font-family: 'Heebo', sans-serif;
        text-align: right;
        line-height: 5vh;
    }

    .subtasks-no-bullet {
        list-style: none;
        padding: 0;
        margin: 0;
    }

    .checkbox-label {
        display: flex;
        align-items: center;
        gap: 10px;
        cursor: pointer;
        font-size: 1rem;
    }

    /* קונטיינר הכפתורים */
    .sections-buttons {
        display: flex;
        flex-wrap: wrap;        /* מאפשר 2 בשורה */
        justify-content: center;
        gap: 3vw;
        direction: rtl;
        margin: 3vh 0 5vh 0;
    }

    /* הכפתורים עצמם */
    .sections-buttons button {
        width: 40vw;            /* 2 כפתורים בשורה */
        font-family: 'Heebo', sans-serif;
        font-size: 2vh;
        padding: 1.2vh 0;
        border-radius: 14px;
        border: 2px solid #0b5394;
        background-color: white;
        color: #0b5394;
        cursor: pointer;
        transition: all 0.25s ease;
        font-weight: 500;
    }

    /* אפקט hover (לטאבלט/דפדפן מובייל) */
    .sections-buttons button:hover {
        background-color: #0b5394;
        color: white;
    }

    /* הכפתור שנלחץ ונשאר כחול */
    .active-btn {
        background: linear-gradient(135deg, #0b5394, #235884);
        color: white !important;
        border: 2px solid #0b5394;
        font-weight: bold;
        box-shadow: 0 6px 18px rgba(11, 83, 148, 0.35);
        transform: scale(1.03);
    }
    
}

/* Tablet */
@media screen and (min-width: 769px) and (max-width: 1024px) {}

