}

/* עיצוב בסיסי של כל הכפתורים */
.sections-buttons button {
    font-family: 'Heebo', sans-serif;
    font-size: 2.2vh;
    padding: 1vh 0.5vw;
    border-radius: 14px;
    border: 2px solid #0b5394;
    background-color: white;
    color: #0b5394;
    cursor: pointer;
    transition: all 0.25s ease;
    min-width: 12vw;
    font-weight: 500;
}

/* אפקט מעבר עכבר */
.sections-buttons button:hover {
    background-color: #0b5394;
    color: white;
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(11, 83, 148, 0.25);
}

/* עיצוב הכפתור הנבחר (לחוץ) */
.active-btn {
    background: linear-gradient(135deg, #0b5394, #235884);
    color: white !important;
    border: 2px solid #0b5394;
    font-weight: bold;
    box-shadow: 0 6px 18px rgba(11, 83, 148, 0.35);
    transform: scale(1.05);
}

/* אפקט לחיצה אמיתי */
.sections-buttons button:active {
    transform: scale(0.97);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}



